import React from 'react';
import Search from './search';
import QuickSearch from './quicksearchApi';

const Home = (props) => {
    const handleRestaurant = (data)=>{
        props.history.push(`/rest/${data}`)
    }
    return(
        <div>
            <Search rid={(data) => {handleRestaurant(data)}}/>
            <QuickSearch/>
        </div>
    )
}

export default Home;