import React, {Component} from 'react';
import axios from 'axios';
import ListingDisplay from './listingDisplay';
import CostFilter from '../filters/costfilter';
import CuisineFilter from '../filters/cuisinefilter';


const lurl="https://sagarikaedurekarestaurant.herokuapp.com/restaurant?mealtype=";
const limit =2;
class Listing extends Component{
    constructor(props){
        super(props)

        this.state={
            restListing:'',
            activePage:1,
            totalNoOfItem:1
        }
    }

    setDataPerFilter(sortedData){
        this.setState({restListing:sortedData})
    }

    render(){
        console.log(this.props.match.params.id)
        return(
            <div>
                <div className="row">
                    <div className="col-md-2">
                    <CuisineFilter mealIdNumber={this.props.match.params.id} restpercuisine={(data) => {this.setDataPerFilter(data)}}/>
                    <hr/>
                        <CostFilter restpercost={(data) => {this.setDataPerFilter(data)}} mealIdNumber={this.props.match.params.id}/>
                    </div>
                    <div className="col-md-10">
                        <ListingDisplay restdata={this.state.restListing} activePage={this.state.activePage}
                        limit={limit} totalNoOfItem={this.state.totalNoOfItem} pageNumber={(data) => {this.
                        setState({activePage:data})}}/>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount(){
        var mealId=this.props.match.params.id;
        sessionStorage.setItem('type',mealId);
        fetch(`${lurl}${mealId}`)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({
                restListing:data.slice(0,data.length-1),
                totalNoOfItem: data.length-1
            })
        })
    
       
    }
}

export default Listing;